import MyAlgoConnect from '@randlabs/myalgo-connect';
import algosdk, { Transaction } from "algosdk";
import { IWallet, Networks, PopupPermissionCallback, SignedTxn } from "../_types";

const logoInverted =
  "data:image/png;base64,";
const logo =
  "data:image/png;base64,";

/*
 Warning: 
    Popups will be blocked if the connect or sign are called outside of a user triggered event, please make sure 
    the user allows all popups or the calls are all triggered by events
    https://connect.myalgo.com/docs/getting-started/important-considerations#browser-events-and-blocked-popups
*/

class MyAlgoWallet implements IWallet {
  accounts: string[];
  defaultAccount: number;
  permissionCallback?: PopupPermissionCallback;
  walletConn: MyAlgoConnect;
  network?: Networks;

  constructor() {
    this.accounts = [];
    this.defaultAccount = 0;

    this.walletConn = new MyAlgoConnect();
  }

  static displayName(): string {
    return "My Algo";
  }
  displayName(): string {
    return MyAlgoWallet.displayName();
  }

  static img(inverted: boolean): string {
    return inverted ? logoInverted : logo;
  }

  img(inverted: boolean): string {
    return MyAlgoWallet.img(inverted);
  }

  async connect(): Promise<boolean> {
    if (this.isConnected()) return true;

    try {
      const accounts = await this.walletConn.connect();
      this.accounts = accounts.map((account) => account.address);
    } catch (err) {
      return false;
    }

    return true;
  }

  isConnected(): boolean {
    return this.accounts && this.accounts.length > 0;
  }

  disconnect() {
    /* noop */
  }

  getDefaultAccount(): string {
    if (!this.isConnected()) return "";
    return this.accounts[this.defaultAccount];
  }

  async doSign(defaultAcct: string, txns: Transaction[]): Promise<SignedTxn[]> {
    const unsigned = [];
    const signedTxns = [];
    for (const tidx in txns) {
      if (!txns[tidx]) continue;

      const txn = txns[tidx];
      if (algosdk.encodeAddress(txn.from.publicKey) === defaultAcct) {
        signedTxns.push(unsigned.length);
        unsigned.push(txn.toByte());
      } else {
        signedTxns.push({ txID: "", blob: new Uint8Array() });
      }
    }

    const s = await this.walletConn.signTransaction(unsigned);
    for (let x = 0; x < signedTxns.length; x++) {
      if (typeof signedTxns[x] === "number") {
        signedTxns[x] = s[signedTxns[x] as number];
      };
    }

    return signedTxns as SignedTxn[];
  }

  async signTxn(txns: Transaction[]): Promise<SignedTxn[]> {
    const defaultAcct = this.getDefaultAccount();

    if (this.permissionCallback) {
      return await this.permissionCallback.request({
        approved: async (): Promise<SignedTxn[]> => {
          return await this.doSign(defaultAcct, txns);
        },
        declined: async (): Promise<SignedTxn[]> => {
          return [];
        },
      });
    }

    return await this.doSign(defaultAcct, txns);
  }

  signBytes(
    b: Uint8Array,
    permissionCallback?: PopupPermissionCallback
  ): Promise<Uint8Array> {
    throw new Error("Method not implemented." + b + permissionCallback);
  }

  async signTeal(
    teal: Uint8Array,
    _permissionCallback?: PopupPermissionCallback
  ): Promise<Uint8Array> {
    return await this.walletConn.signLogicSig(teal, this.getDefaultAccount());
  }
}

export default MyAlgoWallet;
